#intro {
    height: calc(100vh- 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.bg {
    position: absolute;
    top: 4rem;
    right: 7rem;
    z-index: -1;
    object-fit: cover;
    border-radius: 5rem;
}

.introContent {
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello {
    font-size: 1.75rem;
    font-weight: 100;

}

.introName {
    color: yellow;

}

.introPara {
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}

@media screen and (max-width:480px) {
    .bg {
        right: -20vw;
    }
